import React, { useState } from 'react';
import { useObserver, observable } from 'mobx-react'
import useStore from '../useStore';
import BoardList from './BoardList';
import BoardListAll from './BoardListAll';

import Community from './Community';

const SearchBar = () => {

    const { search, list } = useStore();
    const [ input, setInput ] = useState('');

    const onChange = (e) => {
        setInput(e.target.value);
    }

    const onKeyPress = (e) => {
        if(e.key == 'Enter') {
            onClick(e);
        }
    }

    const onClick = (e) => {
        search.setInput(e.target.value);
        setInput("");
    }
    return useObserver (()=>(
        <div>
                <input type="text" placeholder="커뮤니티 검색" value={input}
                    onChange={onChange} onKeyPress={onKeyPress}/>
                <div>
                    {search.input}
                    {search.input === "" ? <BoardListAll/> : <BoardList input={search.input}/>}
                </div>
        </div>
    ));
}

export default SearchBar;











